
@font-face {
    font-family: 'rooney_probold_italic';
    src: url('../fonts/Rooney/rooneypro-bolditalic-webfont.eot');
    src: url('../fonts/Rooney/rooneypro-bolditalic-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/Rooney/rooneypro-bolditalic-webfont.woff2') format('woff2'),
         url('../fonts/Rooney/rooneypro-bolditalic-webfont.woff') format('woff'),
         url('../fonts/Rooney/rooneypro-bolditalic-webfont.ttf') format('truetype'),
         url('../fonts/Rooney/rooneypro-bolditalic-webfont.svg#rooney_probold_italic') format('svg');
    font-weight: normal;
    font-style: normal;

}

