.module {
    transform: scale(0.7, 0.7);
    visibility: hidden;
    overflow: hidden;
    opacity: 0;
    position: relative;
    transition: all 0.5s ease;
    &.module--dark {
        background-color: $c-grey-25;
        h2,
        p {
            color: white;
        }
    }
    &.module--dark-mobile {
        @media handheld,
        screen and (min-width: 0px) and (max-width:$bp-desktop) {
            background-color:$c-bg;
        }
    }
    &.module--dark-mobile-news {
        @media handheld,
        screen and (min-width: 0px) and (max-width:$bp-desktop) {
            background-color:$c-grey-25;
        }
    }
    .info-container {
        display: none;
    }
    .module__text {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        -webkit-transform: translateY(-50%);
        padding: 0 12%;
        p.first-para {
            margin: 20px 0px 0px 0px;
        }
        p {
            @media handheld,
            screen and (min-width: 0px) and (max-width:$bp-phone) {
                font-size:14px;
            }
            @media handheld,
            screen and (min-width:$bp-phone) and (max-width:$bp-desktop) {
                font-size: 16px;
            }
        }
    }
    .home--tweet {
        width: 100%;
    }
    &:hover,
    &.hover {
        .overlay {
            opacity: 1;
        }
    }
    .ratio {
        transition: transform 0.8s ease 0s, opacity 0.8s ease 0s;
        transform: scale(1);
    }
    a,
    a.hover {
        &:hover .ratio {
            transform: scale(1.05);
        }
    }
    &.module--staff:hover .ratio {
        transform: scale(1);
    }
    &.module--job {
        text-align: center;
        background-color: $c-bg;
        width: 100%;
        padding-top: 100%;
        @media handheld,
        screen and (min-width: $bp-desktop) {
            width: 50%;
            padding-top: 50%;
        }
        @media handheld,
        screen and (min-width: $job-breaks) and (max-width: $bp-desktop) {
            width: 50%;
        }
        @media handheld,
        screen and (min-width:0px) and (max-width: $job-breaks) {
            width: 100%;
        }
        &:hover {
            #admin #shapes .st0,
            #motion #wobbly,
            #designer #designer_shapes .st1 {
                stroke: #fff;
            }
            #admin #circles .st3,
            #motion .st0,
            #designer #designer_circles .st0 {
                stroke: #000;
            }
        }
        .module__text {
            @media handheld,
            screen and (min-width: 0px) and (max-width: $bp-desktop) {
                padding: 0;
            }
        }
        .icon-holder {
            width: 75%;
            margin: 0px auto;
            @media handheld,
            screen and (min-width: 0px) and (max-width: $job-breaks) {
                width: 50%;
            }
        }
        h2+p {
            padding-top: 2%;
        }
        h2 {
            color: $c-tilt-pink;
            @include font-size(25);
        }
        &:hover {
            background-color: $c-tilt-pink;
            h2,
            h3 {
                color: white;
            }
        }
    }
    & .module__description {
        height: 100%;
        bottom: 0px;
        top: auto;
        transform: none;
        padding: 0;
        left: 0;
        right: 0;
        margin: 0px auto;
        img {
            width: 65%;
            padding-bottom: 7.5%;
            padding-top: 7.5%;
            &.careers--img {
                width: 50%;
            }
        }
        .careers & {
            @extend %clearfix;
        }
        /* .careers & { margin:0 0 50px 0; } */
    }
    &.module--no-bg {
        background-color: transparent;
    }
    &.module--no-zoom {
        .ratio {
            &:hover {
                transform: none;
            }
        }
    }
}

.module--visible {
    transform: scale(1);
    visibility: inherit;
    opacity: 1;
}

.module--2-1 {
    width: 100%;
    padding-top: 50%;
    clear: left;
}

.module--16-9 {
    width: 100%;
    clear: left;
    padding-top: 56%;
}

.module--16-5 {
    width: 100%;
    clear: left;
    padding-top: 31.33333%;
    min-height: 400px;
    & #about-page--video {
        object-fit: cover;
    }
}

.module--1-1 {
    width: 50%;
    padding-top: 50%;
    background-color: $c-bg;
    float: left;
}

#module-other {
    background-color: yellow;
}

.module--2-2 {
    width: 100%;
    padding-top: 100%;
    float: left;
    &.careers--image {
        padding-top: 90%;
    }
}

.module--staff {
    // width: 50%;
    width: 100%;
    padding-top: 100%;
    height: 1px;
    float: left;
    @media handheld,
    screen and (min-width: $bp-desktop) {
        width: 25%;
        padding-top: 25%;
    }
}

.module--staff__director {
    width: 100%;
    padding-top: 100%;
    height: 1px;
    background-color: #CCCCCC;
    float: left;
    @media handheld,
    screen and (min-width: $bp-desktop) {
        width: 33.33333%;
        padding-top: 33.33333%;
    }
}

.ratio {
    bottom: 0px;
    left: 0px;
    position: absolute;
    right: 0px;
    top: 0px;
    background-size: cover;
}

#instagram_link_1,
#instagram_link_2,
#instagram_link_3,
#instagram_link_4 {
    &:after {
        content: "";
        height: 23px;
        width: 23px;
        position: absolute;
        background-image: url('../images/instagram_transparent.png');
        opacity: 0.8;
        bottom: 20px;
        right: 20px;
    }
}

.news--icon {
    &:after {
        content: "";
        height: 25px;
        width: 24px;
        position: absolute;
        background-image: url('../images/news_icon.png');
        opacity: 0.8;
        bottom: 20px;
        right: 20px;
    }
}

.logo--icon {
    &:after {
        content: "";
        position: absolute;
        opacity: 0.6;
        bottom: 20px;
        left: 20px;
    }
}


