.module--video {
    overflow: hidden;
    @extend %clearfix;
    .ratio {
        &:hover {
            transform: none;
        }
    }
    video {
        &:hover {
            transform: none;
        }
    }
    video {
        position: absolute;
        top: -9999px;
        right: -9999px;
        bottom: -9999px;
        left: -9999px;
        display: block;
        min-width: 100%;
        height: auto;
        min-height: 100%;
        margin: auto;
        transition: transform 0.8s ease 0s, opacity 0.8s ease 0s;
        transform: scale(1);
        overflow: hidden;
        header & {
            position: absolute;
            top: -9999px;
            right: -9999px;
            bottom: -9999px;
            left: -9999px;
            display: block;
            min-width: 100%;
            height: auto;
            min-height: 100%;
            margin: auto;
            width: 100%;
            &:hover {
                transform: none;
            }
        }
    }
    &.module--staff {
        cursor: pointer;
        video {
            height: 100%;
            width: 100%;
            &:hover {
                transform: scale(1);
            }
        }
    }
}

.overlay {
    font-size: 1em;
    position: absolute;
    background-color: rgba(32, 32, 32, 0.7);
    top: 0;
    height: 100%;
    width: 100%;
    z-index: 3;
    opacity: 0;
    transition: opacity 0.5s ease;
}

.overlay-text {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    padding: 0 percentage(126/1016);
}

.overlay--staff {
    border-bottom: 10px solid $c-tilt-pink;
    background-color: rgba(0, 0, 0, 0.2);
    & h2.underlined {
        margin-bottom: 0px;
        &:after {
            width: em(300, 75);
        }
    }
    .overlay-text {
        left: 0px;
        top: auto;
        bottom: 5%;
        @media handheld,
        screen and (min-width: $bp-phone) {
            bottom: auto;
            top: 51%;
        }
    }
}

.fullpage-overlay {
    display: none;
    height: 100vh;
    width: 100%;
    background-color: black;
    position: fixed;
    z-index: 9999;
    video {
        display: block;
        margin: 0px auto;
        width: 100%;
        max-height: 100vh;
        cursor: pointer;
    }
}

.module--image {
    padding-top: 0px;
}

.module--text-pad {
    padding-top: 100%;
    @media handheld,
    screen and (min-width: $bp-desktop) {
        padding-top: 50%;
    }
    &.module--mobile-double-height {
        @media handheld,
        screen and (min-width: 0px) and (max-width:$bp-phone) {
            padding-top: 125%
        }
    }
}

#staff-member {
    transition: all 0.5s ease;
    display: none;
}

#staff-member__close {
    position: absolute;
    cursor: pointer;
    top: 25px;
    right: 25px;
    height: 0;
    width: 17px;
    height: 17px;
    background: url('../images/close_btn.png') top left no-repeat;
    background-size: 17px 17px;
    overflow: hidden;
    cursor: pointer;
    z-index: 999;
    opacity: 0;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 17px;
        height: 17px;
        background: url('../images/close_btn_hover.png') top left no-repeat;
        background-size: 17px 17px;
        transition: all .2s;
        opacity: 0;
    }
    &:hover:before {
        opacity: 1;
    }
}

#staff-member__info {
    overflow: auto;
    opacity: 0;
    transition: opacity 0.5s ease;
    width: 100%;
    float: right;
    padding: 0% 5% 0% 0%;
    height: auto;
    top: 50%;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    @media handheld,
    screen and (min-width: $bp-desktop) {
        width: 50%;
    }
}

#staff-member__wrapper {
    @extend %clearfix;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: opacity 0.5s ease;
}

#video-overlay-close {
    padding: 67px 0px 0px 0px;
    height: 0;
    width: 68px;
    background: url('../images/close_btn_video.png') 0px 0px no-repeat;
    margin: 0px auto;
    position: absolute;
    top: 0px;
    right: 0px;
    text-align: center;
    cursor: pointer;
    z-index: 9999;
    &:hover {
        background: url('../images/close_btn_video.png') 0px -67px no-repeat;
    }
    & p {
        font-family: $f-seroot;
    }
}